.give-fields-group
  margin: 16px -15px
  +clr
  &:first-child
    margin-top: 0
  &:last-child
    margin-bottom: 0
  &._tripple
    margin-left: -8px
    margin-right: -8px
    .give-fields-group__cell
      width: 33.3%
      padding-left: 8px
      padding-right: 8px

.give-fields-group__cell
  float: left
  width: 50%
  box-sizing: border-box
  padding-left: 15px
  padding-right: 15px

.give-field__label
  position: absolute
  z-index: 0
  top: 50%
  left: 45px
  max-width: 100%
  box-sizing: border-box
  +text-overflow
  +f(t, 15)
  line-height: 22px
  margin-top: -11px
  cursor: text
  transition: all 0.3s ease
  color: inherit

.give-field__input
  display: block
  height: 40px
  width: 100%
  border: 1px solid $color-border-grey
  box-sizing: border-box
  background: none
  // padding: 11px 8px 12px 44px
  padding: 0 8px 0 44px
  border-radius: 3px
  +f(b, 15)
  color: $color-text
  box-shadow: none
  appearance: none
  +trans()
  +placeholder
    +f(t)
    color: #9B9B9B
  &:focus
    outline: none
    border-color: $color-blue
    background: #FFF
    box-shadow: 0px 0px 4px 0px rgba(73,143,225,0.50)
    // & ~ .give-field__left-icon
    //   color: $color-blue
    // & ~ .give-field__label
    //   color: $color-blue
  &._phone
    padding-left: 86px
  &.is-valid
    border-color: $color-green
    & ~  .give-field__left-icon
      color: $color-green

.give-field
  position: relative
  box-sizing: border-box
  color: $color-border-grey
  &._phone
    .give-field__input
      padding-left: 86px
    .give-field__label
      left: 86px

  &._no-left-icon
    .give-field__input
      padding-left: 8px
    .give-field__label
      left: 8px

  &._right-icon
    .give-field__input
      padding-right: 36px
    .give-field__label
      padding-right: 36px

  &._solid-bg
    .give-field__input
      background: #FFF
      border-color: $color-text
      &:focus
        box-shadow: none
        border-color: $color-blue

  &._simple
    .give-field__input
      border-color: $color-text
      padding-left: 8px
    .give-field__label
      left: 8px

  &.is-focus
    color: $color-blue

  &.is-filled
    .give-field__label
      transform-origin: left bottom
      transform: translateY(-29px) scale(0.8)
      // background: rgba(#F6F6F6, 0.43)
      padding: 0 5px
      background: #FFF

  &.is-valid
    color: $color-green
    .give-field__input
      border-color: $color-green
    .give-field__left-icon
      color: $color-green

  &.is-error
    color: $color-red
    animation: give-shake 1s linear 1
    .give-field__input
      border-color: $color-red
      box-shadow: inset 0 0 0 1px $color-red
    .give-field__left-icon
      color: $color-red
    .give-field__input
      color: $color-red
    .give-tooltip
      background: $color-red
      &:after
        background: $color-red

.give-field__left-icon
  position: absolute
  z-index: 0
  top: 0
  left: 0
  bottom: 0
  width: 36px
  line-height: 38px
  text-align: center
  font-size: 20px
  +trans(color)
  &:after
    content: ""
    position: absolute
    top: 5px
    bottom: 5px
    width: 1px
    right: 0
    background: $color-border-grey

.give-field__right-icon
  position: absolute
  top: 0
  right: 0
  bottom: 0
  font-size: 20px
  line-height: 38px
  text-align: center
  width: 36px
  color: $color-border-grey
  &:before
    position: relative
  &._eye-open
    // font-size: 9px
    font-size: 16px
    cursor: pointer
  &._eye-closed
    // font-size: 7px
    font-size: 16px
    cursor: pointer
    &:before
      top: 2px

.give-field__select
  position: absolute
  z-index: 1
  top: 0
  left: 0
  height: 100%
  width: 78px
  border: none
  &:after
    content: ""
    position: absolute
    top: 5px
    bottom: 5px
    width: 1px
    right: 0
    background: $color-border-grey
  .give-select
    border-color: transparent
    border-color: rgba(#FFF, 0)
    width: 100%
    padding-left: 8px
    padding-right: 22px
    &:after
      right: 8px
