.give-range
  .noUi-target
    border: none
    box-shadow: none
    border-radius: 2px
    margin: 0 8px

  .noUi-horizontal
    height: 4px

  .noUi-background
    background: $color-dark
    box-shadow: 6px 0 0 $color-dark
    +gpu

  .noUi-connect
    background: $color-green
    box-shadow: -6px 0 0 $color-green

  .noUi-handle
    width: 16px
    height: 16px
    border-radius: 50%
    left: -8px
    top: -6px
    box-shadow: none
    border: none
    background: $color-blue
    +trans(background-color)
    &:before,
    &:after
      display: none

  &.is-active
    .noUi-handle,
    .noUi-connect
      background: $color-green
