body
  margin: 0

.give
  color: $color-text
  +f(t, 18)
  min-width: 320px
  box-sizing: border-box
  +smoothfont
  font-size-adjust: none
  button
    background: none
    border: none
    box-shadow: none
    cursor: pointer
    padding: 0
    margin: 0
    user-select: none
    &:focus
      outline: none
  &._with-content
    .give-bg
      +clr
    .give-widget
      float: left
      margin-right: -$widget-max-width
    .give-aside
      float: right
      margin-left: $widget-max-width
    +r(1023)
      .give-widget
        float: none
        margin-right: auto
      .give-aside
        float: none
        margin: auto
        padding-top: 24px

  +mobile
    padding-left: 8px
    padding-right: 8px

.give-bg
  background: rgba(#F6F6F6, 0.43)

.give-l-center
  margin: auto
  max-width: (2*688)+px
