+r(600)
  .give-check-group
    padding-left: 5%

+mobile
  .give-bg
    background: #FFF

  .give-header-links
    padding-left: 0
    padding-right: 0

  .give-tagline
    font-size: 14px
    line-height: 1.28
    padding-left: 0
    padding-right: 0

  .give-widget-body
    padding-left: 0
    padding-right: 0
    .give-thank
      top: -1px
      left: -8px
      right: -8px
      bottom: -1px
      padding: 40px 16px 0
      width: auto
      height: auto
  .give-widget-body__inner
    border: none
    padding: 0
  .give-prev-button,
  .give-next-button
    display: none

  .give-presets
    padding-left: 0
    padding-right: 0
    .give-dots
      display: block
  .give-presets__button
    font-size: 30px
  .give-presets__item
    width: 33.3%
    &:last-child
      width: 33.3%
      border-left: none
      .give-presets__button
        font-size: 20px

  .give-designate
    margin: 0 -8px
    padding: 0 8px
    &.is-active
      .give-designate-button
        .give-check__text
          padding-left: 26px
  .give-designate-button
    width: 33.3%
    width: calc(33.3% + 1px)
    .give-check__text
      position: relative
      padding-left: 0
      transition: padding-left 0.3s linear
      &:before
        left: 0
      &:after
        left: 6px

  .give-check-group
    max-width: 300px
    margin-left: auto
    margin-right: auto
    padding: 0 8px
    font-size: 14px
  .give-check
    font-size: 14px
  .give-check-group__cell
    margin-right: 0
    &:last-child
      margin: 6px 0 0 44px

  .give-submit-button
    max-width: 256px

  .give-footer
    padding-left: 8px
    padding-right: 8px

  .give-tabs
    display: block
    white-space: nowrap
    box-sizing: border-box
    position: relative
    overflow: hidden
    border-bottom: 1px solid $color-border-grey
    +shadow-edge
  .give-tabs__list
    display: block
    white-space: nowrap
    overflow: auto
    padding: 0 60px
    +scroll-touch
  .give-tabs__button
    font-size: 18px
    padding-bottom: 10px
    height: 44px
  .give-tabs__item
    display: inline-block
    width: auto
    border: none
    box-shadow: none
    font-size: 18px
    margin: 0 7px
    &.is-active
      .give-tabs__button
        color: $color-blue
        box-shadow: 0 -6px 0 -2px $color-blue inset

  .give-bar-container
    padding: 0
  .give-amount-bar
    padding: 0 40px
    &._amount
      // padding-right: 25px
    &._sum
      .give-amount-bar__center
        text-align: center
      .give-amount-bar__left-label
        display: none
      .give-amount-bar__left-button
        bottom: 20px
        > span
          display: none
  .give-amount-bar__center
    max-width: none
  .give-amount-bar__right-label,
  .give-amount-bar__left-label
    display: none
  .give-amount-bar__right-button
    right: 8px
    text-align: right
    bottom: 15px
    .give-icon
      display: block
  .give-amount-bar__left-button
    left: 8px
    bottom: 15px
    text-align: left
    .give-icon
      display: block

  .give-keyboard__button
    font-size: 30px


  .give-thank__title
    margin-bottom: 10px
    .give-title._size-sm
      font-size: 20px
    & + .give-title
      padding: 0 10px
  .give-thank__picture
    display: block
    margin: 0
  .give-thank__footer
    display: none
  .give-thank__stores
    margin-top: 0
    > br
      display: none
  .give-thank__store-link
    &:not(:last-child)
      margin-right: 13px

  .give-payment-form
    padding: 0
  .give-payment-form__footer
    text-align: center
    margin-top: 24px
    .give-secured-label
      position: relative
      top: 0
      left: 0
      margin: 5px auto 0

  .give-fields-group
    margin: 0
    padding: 0 8px
    &._tripple
      .give-fields-group__cell
        float: left
        &:first-child
          float: none
          width: auto
        &:nth-child(2)
          width: percentage(132/304)
        &:last-child
          width: percentage(172/304)
  .give-fields-group__cell
    width: auto
    padding: 0
    float: none
    margin: 0 0 16px 0

  .give-payment-tabs-content
    padding: 0 8px
  .give-payment-tabs-slide
    .give-card-box
      margin: 0 auto 15px
      float: none

  .give-ach
    .give-fields-group
      padding: 0
      &:last-child
        .give-fields-group__cell:last-child
          margin-bottom: 0
  .give-ach__row
    margin: 3px 0 13px

  .give-designate__inner
    padding-left: 0
    padding-right: 0

  .give-range
    padding: 0 8px
    &._optional
      .give-range__row
        &:last-child
          padding-left: 26px
      .give-range__remove
        display: block
        opacity: 0.5
        visibility: visible
        left: 7px
  .give-range__remove
    display: none

  .give-payment-tabs
    border-top: none
    border-bottom: 1px solid $color-border-grey
    padding-left: 8px
    padding-right: 8px
    margin-left: -8px
    margin-right: -8px
    +shadow-edge
  .give-payment-tabs__button
    font-size: 18px
    &.is-active
      color: $color-blue

  .give-recurring-gift
    vertical-align: top
  .give-recurring-gift__cell
    &:last-child
      margin: 6px 0 0 44px

  .give-in-memory-of
    margin: 6px 0 0 33px

  .give-aside
    padding-left: 8px
    padding-right: 8px

