.give-thank
  position: relative
  padding: 99px 15px 80px
  box-sizing: border-box
  min-height: 100%
  background: #FFF
  text-align: center
  border-radius: 3px

.give-thank__close
  position: absolute
  top: 0
  right: 0
  font-size: 13px
  padding: 12px
  color: $color-text
  opacity: 0.5
  +trans
  &:hover
    opacity: 1

.give-thank__header
  margin-bottom: 23px
  .give-title
    margin-bottom: 0

.give-thank__title
  display: block
  line-height: 1.85
  margin: 0 0 31px 0

.give-thank__body
  text-align: center

.give-thank__footer
  margin-top: 29px
  font-size: 16px
  line-height: 1.25
  p
    margin: 0
  b, strong
    +f(b)
  .give-thank-form
    left: 12px

.give-thank__stores
  display: inline-block
  vertical-align: middle
  font-size: 0
  margin-top: 10px

.give-thank__store-link
  display: inline-block
  margin-top: 17px
  &:first-child
    margin-top: 0
  img
    display: block

.give-thank__picture
  display: inline-block
  vertical-align: middle
  margin: 0 30px 0 -55px
  img
    display: block
    margin: auto


.give-thank-form
  display: block
  box-sizing: border-box
  position: relative
  max-width: 328px
  height: 40px
  margin: 0 auto 27px
  border-radius: 3px

.give-thank-form__input
  display: block
  width: 100%
  height: 100%
  box-sizing: border-box
  padding: 0 127px 0 15px
  background: none
  appearance: none
  border: 1px solid $color-text
  border-radius: 3px
  box-shadow: none
  outline: none
  +f(b, 15)
  color: $color-text
  +trans
  +placeholder
    +f(t, 15)
  &:focus
    border-color: $color-blue
    box-shadow: 0px 0px 4px 0px rgba(73,143,225,0.50)

.give-thank-form__button
  position: absolute
  top: 0
  right: 0
  height: 100%
  box-sizing: border-box
  border-radius: 3px
  background: $color-text
  width: 112px
  text-align: center
  +f(t, 15)
  color: #FFF


.give-thank-sms
  display: inline-block
  position: relative
  line-height: 28px
  padding: 0 7px
  box-sizing: border-box
  min-width: 52px
  border: 1px solid
  border-radius: 16px
  background: #FFF
  &:before
    content: ""
    position: absolute
    z-index: 0
    bottom: -4px
    right: 5px
    width: 10px
    height: 10px
    background: #FFF
    border: 1px solid
    border-width: 0 0 1px 1px
    transform: rotate(-63deg)
  > *
    position: relative
    z-index: 1
