@function em($size, $context: 16)
  @return ($size/$context) * 1em

@function img-url($image)
  @return url('../img/#{$image}')

=image($image)
  background-image: img-url($image)

=trans($what: all, $dur: 0.15s, $easing: ease)
  transition: $what $dur $easing

=scroll-touch
  -webkit-overflow-scrolling: touch

=smoothfont
  -webkit-font-smoothing: antialiased
  -moz-font-smoothing: antialiased

=gpu
  transform: translate3d(0,0,0)

=md
  +r(1023)
    @content

=sm
  +r(767)
    @content

=mobile
  +r(567)
    @content

=shadow-edge
  &:before,
  &:after
    content: ""
    position: absolute
    z-index: 1
    top: 0
    height: 100%
    width: 60px
    pointer-events: none
  &:before
    left: -1px
    background: linear-gradient(to right, rgba(white, 1) 0%, rgba(white, 0) 100%)
  &:after
    right: -1px
    background: linear-gradient(to left, rgba(white, 1) 0%, rgba(white, 0) 100%)

