.give-other-amount
  padding: 18px 15px 8px
  position: relative

.give-bar-container
  margin: 18px 0 20px
  padding: 0 15px
  position: relative

.give-amount-bar
  position: relative
  border-radius: 3px
  height: 64px
  background: $color-text
  color: #FFF
  user-select: none
  &._sum
    background: $color-green
    .give-amount-bar__left-label,
    .give-amount-bar__right-label
      opacity: 0.6
      font-size: 14px
    .give-amount__after-point
      opacity: 1

.give-amount-bar__center
  text-align: center
  max-width: 50%
  height: 100%
  line-height: 100%
  margin: auto
  &:before
    content: ""
    display: inline-block
    vertical-align: middle
    height: 100%
    margin-left: -0.25em

.give-amount-bar__left-label,
.give-amount-bar__right-label
  position: absolute
  z-index: 1
  top: 7px
  font-size: 12px
  opacity: 0.5
  line-height: 1

.give-amount-bar__left-label
  left: 16px

.give-amount-bar__right-label
  right: 16px

.give-amount-bar__left-button,
.give-amount-bar__right-button
  position: absolute
  z-index: 1
  bottom: 10px
  +f(t, 14)
  line-height: 1
  color: inherit
  opacity: 0.5
  +trans()
  > *
    vertical-align: middle
  .give-icon._keyboard
    font-size: 22px
  &:hover
    opacity: 1

.give-amount-bar__left-button
  left: 16px

.give-amount-bar__right-button
  right: 16px

.give-amount-bar__change-value
  position: absolute
  z-index: 1
  top: 0
  right: 0
  height: 100%
  width: 33px
  border-left: 1px solid
  color: #FFF
  opacity: 0.6
  +trans(opacity)
  &:before
    content: ""
    position: absolute
    z-index: -1
    top: 50%
    left: 0
    right: 0
    border-top: 1px solid
  .give-icon
    display: block
    height: 50%
    width: 100%
    box-sizing: border-box
    color: #FFF
    font-size: 16px
    opacity: 0.6
    +trans(opacity)
    &:hover
      opacity: 1
    // &._minus
    //   font-size: 2px
  &:hover
    opacity: 1

.give-amount
  display: inline-block
  vertical-align: middle
  position: relative
  top: 3px
  line-height: 1
  color: #FFF
  font-size: 0
  text-align: left
  white-space: nowrap
  // sup
  //   font-size: inherit
  //   vertical-align: super

.give-amount__currency
  display: inline-block
  vertical-align: top
  +f(sb, 29)
  position: relative
  top: em(-1,29)
  opacity: 0.5

.give-amount__before-point
  display: inline-block
  vertical-align: top
  +f(t, 59)
  line-height: 0.8
  min-height: 47px
  padding-right: 3px
  position: relative

.give-amount__after-point
  display: inline-block
  vertical-align: top
  position: relative
  padding: 0 4px 0 12px
  min-height: 30px
  +f(sb, 30)
  top: em(-2,30)
  opacity: 0.5
  +trans(opacity)
  &:before
    position: absolute
    top: em(2,59)
    left: 0
    content: "."
    +f(t, 59)
    line-height: 0.8

.give-focus-in
  opacity: 1
  &:after
    content: ""
    position: absolute
    top: 50%
    right: 0
    width: 3px
    height: em(47,59)
    margin-top: em(-22,59)
    background: $color-blue
    animation: give-cursor 1s ease infinite
