.give-range
  position: relative
  padding: 0 30px
  line-height: 1
  font-size: 15px
  margin: 17px 0
  &:first-child
    margin-top: 0
  &:last-child
    margin-bottom: 0
  &:hover
    .give-range__remove
      opacity: 0.5
      visibility: visible
      &:hover
        opacity: 1
  &.is-active
    .give-range__val
      opacity: 1

.give-range__remove
  position: absolute
  bottom: 1px
  left: 0
  font-size: 14px
  color: #FFF
  opacity: 0.5
  +trans()
  opacity: 0
  visibility: hidden
  &:hover
    opacity: 1

.give-range__row
  margin: 5px 0
  min-height: 16px
  +clr
  .noUi-target
    top: 6px

.give-range__label
  position: relative
  top: 5px
  float: left

.give-range__values
  float: right

.give-range__val
  opacity: 0.3
  +f(b, 20)
  +trans(opacity)

.give-range__val-percent
  opacity: 0.3
  &:before
    content: "/ "

.give-form
  margin: 0
  padding: 0 15px 32px
